.footer-background{
    background-color:#111111;
    color:#fff;
    bottom: 0;
    width: 98vw;
}

.footer{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
}

.foot-col{
    font-display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
}

.foot-col p {
    font-family: SpartansSB;
    display: block;
    font-size: 12px;
}

.foot-col .bold {
    font-weight: 900;
}