.item-container {
  background-color: #111111;
  height: 761px;
  margin-right: 164px;
  .item-left {
    margin: 205px 125px 288px 0px;
    .left-item-img{
      width:71px;
      margin-bottom: 50px;
    }
    h3 {
      font-family: SpartansB;
      width: 15ch;
      line-height: 56px;
      letter-spacing: -0.5px;
    }
    p {
      width: 445px;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 30px;
      letter-spacing: -0.225px;
    }
  }
  .item-right {
    margin: 200px 0px 120px 0px;
    .item-list:first-child {
      margin-top: 50px;
    }
    .item-img {
      width: 128px;
    }
    .brown-line {
      margin-right: 30px;
      width: 32px;
      height: 1px;
      left: 699px;
      top: 2552px;
      background: #9e7f66;
    }
    .item-card {
        border-bottom: 1px solid rgba(255, 255, 255, 0.192);
        margin-top: 24px;
        margin-bottom: 24px;
      .item-text {
        align-items: flex-start;
        h4 {
          font-size: 18px;
          line-height: 28px;
          letter-spacing: -0.225px;
        }
        p {
          width: 25ch;
          font-family: SpartansL;
          font-size: 15px;
          line-height: 28px;
          letter-spacing: -0.1875px;
          text-align: left;
        }
      }
    }
  }
}
